import { licensesTypes} from './licenses.types'

export const setCurrentLicense = string => ({
  type: licensesTypes.SET_CURRENT_LICENSE,
  payload: string
});

export const setAutoRenewPersonal = bool => ({
  type: licensesTypes.SET_AUTO_RENEW_PERSONAL,
  payload: bool
});

export const setAutoRenewCorporate = bool => ({
  type: licensesTypes.SET_AUTO_RENEW_CORPORATE,
  payload: bool
});


export const setOptionsLicenseMenu = bool => ({
  type: licensesTypes.SET_OPTIONS__LICENSE_MENU,
  payload: bool
});


export const setCurrentTab = string => ({
  type: licensesTypes.SET_CURRENT_TAB,
  payload: string
});

export const setAdditionalScreenCorporate = bool => ({
  type: licensesTypes.SET_ADDITIONAL_SCREEN_CORPORATE,
  payload: bool
});

