import React, {Fragment} from 'react';
import './style.scss';

import { useHistory } from "react-router-dom";
import {setLanguageModal, setRefillBalanceModal} from "../../../redux/app/app.actions";
import {connect} from "react-redux";


const UserCenter = (props) => {

    let history = useHistory();
    let path = history.location.pathname;
    let userBalanceClass = 'userBalance';
    let UserNameClass = 'userName';
    let balanceString = props.currentUser.userBalance;
    let balanceArray = balanceString.split('.');

    const toProfile = (e) => {
        if(e.target.classList.value !== 'add-button') {
            history.push("/profile");
        }
    }

    const clickHandler1 = (e) => {
        if(e.target.classList.value !== 'add-button') {
            history.push("/operations");
        }
    }


    let currentLang = props.appState.appLanguage.currentAppLanguage;
    let langListClone = [...props.appState.appLanguage.languagesInterface];


    let langListContent = langListClone.map((item)=>{
        let current = '';
        if(item.value === currentLang){
            current = 'currentLang';
        }

        return (<div key={item.toString()} className={current}><img src={item.icon} alt=""/> {item.title}</div>)
    });

    let currentLangObj = langListClone.find((langObj) => {
        return langObj.value === currentLang;
    });


    switch(path) {
        case '/operations':
            userBalanceClass = 'userBalance userBalance--active';
            break;
        case '/transactions':
            userBalanceClass = 'userBalance userBalance--active';
            break;
        case '/profile':
            UserNameClass = 'userName userName--active';
            break;
    }



    return (
        <div className="usercenter" >
            <div className={userBalanceClass} onClick={clickHandler1} >
                <svg width="12" height="21" viewBox="0 0 12 21" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M8.61719 9.99219L4.08594 8.66406C3.46094 8.50781 2.99219 8 2.875 7.375C2.71875 6.35938 3.5 5.5 4.47656 5.5H7.32812C7.95312 5.5 8.57812 5.69531 9.08594 6.08594C9.32031 6.24219 9.67188 6.20312 9.86719 6.00781L10.7656 5.10938C11.0391 4.83594 11 4.40625 10.7266 4.17188C9.75 3.42969 8.57812 3 7.32812 3H7.25V1.125C7.25 0.8125 6.9375 0.5 6.625 0.5H5.375C5.02344 0.5 4.75 0.8125 4.75 1.125V3H4.63281C2.71875 3 0.921875 4.25 0.492188 6.125C-0.0546875 8.3125 1.27344 10.4609 3.34375 11.0469L7.875 12.375C8.5 12.5312 9.00781 13.0391 9.08594 13.6641C9.24219 14.6406 8.46094 15.5 7.52344 15.5H4.63281C4.00781 15.5 3.38281 15.3047 2.875 14.9531C2.64062 14.7969 2.28906 14.8359 2.09375 15.0312L1.19531 15.9297C0.921875 16.2031 0.960938 16.6328 1.23438 16.8672C2.21094 17.6094 3.38281 18 4.63281 18H4.75V19.875C4.75 20.2266 5.02344 20.5 5.375 20.5H6.625C6.9375 20.5 7.25 20.2266 7.25 19.875V18H7.32812C9.24219 18 11.0391 16.7891 11.4688 14.9141C12.0156 12.7266 10.6875 10.5781 8.61719 9.99219Z" fill="white" fillOpacity="0.6"/>
                </svg>
                <span>{balanceArray[0]}</span>
                <span className="afterpoint">.{balanceArray[1]}</span>

                <div className="add-button" onClick={()=>{props.setRefillBalanceModal(true)}}></div>
            </div>

            <div className={UserNameClass} >
                <div className="userName__data" onClick={toProfile}>
                    <img src={props.currentUser.photo} alt="userPhoto" />
                    <span>{props.currentUser.name}</span>
                </div>

                <div className="appLanguage" onClick={() => {props.setLanguageModal(true)}}>
                    <img src={currentLangObj.icon} alt=""/>
                    {currentLangObj.value}
                </div>
            </div>

        </div>
    );
};

const mapStateToProps = (state) => ({
    currentUser: state.user.currentUser,
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {
        setLanguageModal: (bool) => dispatch(setLanguageModal(bool)),
        setRefillBalanceModal: (bool) => dispatch(setRefillBalanceModal(bool))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(UserCenter);
