import React from 'react';
import './style.scss';
import {NavLink, useHistory} from "react-router-dom";
import { IconPack } from "../../../Images/IconPack";
import {setAdditionalScreenCorporate} from "../../../redux/licenses/licenses.actions";
import {connect} from "react-redux";

const Menu = (props) => {
    let history = useHistory();
    let path = history.location.pathname;
    let supportItem, dealItem, licenseItem, docItem;



    if( path === '/tickets' || path === '/new-ticket' ) {
        supportItem = (<NavLink to='/tickets' className='menu__item--active'>
            { IconPack.menuIcons.supportActive }
            Support
        </NavLink>);
    }else {
        supportItem = (<NavLink to='/tickets' >
            { IconPack.menuIcons.support }
            Support
        </NavLink>);
    }

    if( path === '/deals' || path === '/new-deal' ) {
        dealItem = (<NavLink to='/deals' className='menu__item--active'>
            { IconPack.menuIcons.guarantorActive }
            Guarantor
        </NavLink>);
    }else {
        dealItem = ( <NavLink to='/deals' >
            { IconPack.menuIcons.guarantor }
            Guarantor
        </NavLink> );
    }

    const userId = 1234567890;
    const licensePath = '/licenses/' + userId + '/';



    if( path === licensePath + 'activity' || path === '/licenses/new' || path === licensePath + 'users' || path === licensePath + 'costs'  ) {
        licenseItem = (  <NavLink to={'/licenses/new'} className='menu__item--active' onClick={()=>{props.setAdditionalScreenCorporate(false)}} >
            { IconPack.menuIcons.licenseActive }
            Licenses
        </NavLink>);
    }else {
        licenseItem = (   <NavLink to={'/licenses/new'} onClick={()=>{props.setAdditionalScreenCorporate(false)}} >
            { IconPack.menuIcons.license }
            Licenses
        </NavLink>);
    }

    if( path === '/documentation'  ) {
        docItem = (  <NavLink  to={'/documentation'} className='menu__item--active'>

            { IconPack.menuIcons.documentation1 }
            Documentation
            { IconPack.menuIcons.documentation2 }

        </NavLink>);
    }else {
        docItem = (   <NavLink to={'/documentation'} >
            { IconPack.menuIcons.documentation1 }
            Documentation
            { IconPack.menuIcons.documentation2 }
        </NavLink>);
    }
    let instItem
    if( path === '/installer'  ) {
        instItem = ( <NavLink to='/installer' className='menu__item--active'>
            { IconPack.menuIcons.installerActive }
            Installer
        </NavLink>);
    }else {
        instItem = ( <NavLink to='/installer' >
            { IconPack.menuIcons.installer }
            Installer
        </NavLink>);
    }
    let faqItem
    if( path === '/general'  ) {
        faqItem = ( <NavLink to='/general' className='menu__item--active'>
            { IconPack.menuIcons.faqActive }
            FAQ
        </NavLink>);
    }else {
        faqItem = ( <NavLink to='/general'>
            { IconPack.menuIcons.faq }
            FAQ
        </NavLink>);
    }

    let configItem;
    if( path === '/config-shop'  ) {
        configItem = ( <NavLink to='/config-shop' className='menu__item--active'>
            { IconPack.menuIcons.configShopActive }
            Config Shop
        </NavLink>);
    }else {
        configItem = ( <NavLink to='/config-shop' >
            { IconPack.menuIcons.configShop }
            Config Shop
        </NavLink>);
    }

    return (
        <ul className="menu">
            <li className="menu__item">
                {licenseItem}
            </li>

            <li className="menu__item">
                {configItem}
            </li>
            <li className="menu__item">
                {dealItem}
            </li>
            <li className="menu__item">
                {instItem}
            </li>
            <li className="menu__item">
                {faqItem}
            </li>
            <li className="menu__item">
                {supportItem}
            </li>
            <li className="menu__item menu__item__documentation">
                {docItem}
            </li>
        </ul>
    );
}

const mapStateToProps = (state) => ({
    licenses: state.licenses.licenses,
    currentUser: state.user.currentUser,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setAdditionalScreenCorporate: (bool) => dispatch(setAdditionalScreenCorporate(bool)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Menu);